import React, { Component } from "react";
import DistributionContract from "./contracts/Distribution.json";
import Web3 from "web3";
import {Container,Button,Form,Image,Alert} from "react-bootstrap";
import "./App.css"
class App extends Component {
  state = { code:null, owner:null, contract: null, tokens_minted:null, minted:null, tokens:null, mintTokens:null, address:null, Buyer:null, buy_tokens:null };

  componentDidMount = async () => {
    try {
      // Get network provider and web3 instance.
      const web3 = new Web3("HTTP://127.0.0.1:7545");

      // Use web3 to get the user's accounts.
      const accounts = await web3.eth.getAccounts();

      // Get the contract instance.
      const networkId = await web3.eth.net.getId();
      const deployedNetwork = DistributionContract.networks[networkId];
      const instance = new web3.eth.Contract(
        DistributionContract.abi,
        deployedNetwork && deployedNetwork.address,
      );

      // Set web3, accounts, and contract to the state, and then proceed with an
      // example of interacting with the contract's methods.
      this.setState({ contract: instance});
      console.log(this.state.contract)
       this.state.contract.methods.owner().call()
      .then((result)=>{
        this.setState({owner:result})
      })
     
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(
        `Failed to load web3, accounts, or contract. Check console for details.`,
      );
      console.error(error);
    }
  };

   handleMintChange=(event)=>{
      this.setState({
        mintTokens:event.target.value
      })
    }
  
 handleMintProcess=()=>{
  this.state.contract.methods.mint_tokens(this.state.mintTokens).send({from:this.state.owner})
 }

  handleTokenLeft=()=>{
    
    this.state.contract.methods.tokensLeft().call({from:this.state.owner})
    .then((result)=>{
      this.setState({
        tokens:result
      })
      console.log(result)
    })
  }
  handleAddressChange=(event)=>{
    this.setState({
      address:event.target.value
    })
  }
  handleSearch=()=>{
    this.state.contract.methods.list(this.state.address).call({from:this.state.owner})
    .then((result)=>{
      this.setState({minted:result[0],tokens_minted:result.no_of_tokens})
      console.log(result)
    })
  }
   handleBuyerChange=(event)=>{
    this.setState({
      Buyer:event.target.value
    })
   }
   handleCode=(event)=>{
    this.setState({
      code:event.target.value
    })
   }
   handleBuyTokenChange=(event)=>{
    this.setState({
      buy_tokens:event.target.value
    })
   }
   handleBuyProcess=async()=>{
    const Result= await this.state.contract.methods.BuyToken(this.state.Buyer,this.state.buy_tokens,this.state.code).send({from:this.state.Buyer})
    }

    handleNewOwner=(event)=>{
      this.setState({
        newOwner:event.target.value
      })
          }

    setNewOwner=async()=>{
     await this.state.contract.methods.transferOwnership(this.state.newOwner).send({from:this.state.owner})
     .then(alert("If this step is compeleted then ownership will be transfered to the new Owner and you will loose all your privilages over smart contract."
      ))
     this.state.contract.methods.owner().call()
     .then((result)=>{
      this.setState({owner:result})
     })
     console.log(this.state.owner)
    }

  

 
  render() {
   
    return (
      <div className="App">
      <Container fluid>


      <header className="heading">
      <h1 >Welcome to Smart Tokenization System</h1>
      <small>(Developed By Shreyansh Singh)</small>
        <h3 className="sub_heading">Good to Go!<Image src="cool.gif" width="70px" height="70px"/></h3>
      </header>


      <section className="owner">
      <h2>OWNER's<br/> <small>are welcomed in this section.</small></h2>
      <h3>Current Owner:{this.state.owner}</h3>

      <h3>Transfer OwnerShip:</h3>
       <Form.Control type="text" placeholder="New Owner Address" defaultValue={this.state.newOwner} onChange={this.handleNewOwner}/>
        <Button type="Button" variant="danger" onClick={this.setNewOwner}>SET</Button>

      <h3>Mint tokens:</h3>
       <Form.Control type="number" min="1" placeholder="No of tokens to be minted." defaultValue={this.state.mintTokens} onChange={this.handleMintChange} />
       <Button type="Button" variant="info" onClick={this.handleMintProcess}>MINT</Button>

       <h3>Record of Distribution:</h3>
       <Form.Control type="text" placeholder="address to be searched for." defaultValue={this.state.address} onChange={this.handleAddressChange} />
       <Button type="Button" variant="info" onClick={this.handleSearch}>SEARCH</Button>
       <h4>Minted:{
        this.state.minted?<b>True</b>:<b>False</b>
       }</h4>
       <h4>Token's:{this.state.tokens_minted}</h4>
       
       <h3>Token's Left:</h3>
       <a href="#" onClick={this.handleTokenLeft}>Reveal</a>
       <h3>{this.state.tokens}</h3>

      </section> 

       <section className="client">
       <h3>Welcome, <small>dear coustumer .</small></h3>
       <h3>Buy tokens:</h3>
       <Form.Control type="text" placeholder="Buyer's address" defaultValue={this.state.Buyer} onChange={this.handleBuyerChange}/><br/>
       <Form.Control type="number" min="1" placeholder="No of tokens." defaultValue={this.state.buy_tokens} onChange={this.handleBuyTokenChange}/><br/>
       <i>*If you are a new costumer enter Code below "0" or "1" for existing coustumer</i><br/>
        <Form.Control type="number" min="0" max="1" placeholder="code" defaultValue={this.state.code} onChange={this.handleCode}/>
       <Button type="button" variant="success" onClick={this.handleBuyProcess}>BUY</Button>
      </section>
       </Container>
      </div>
    );
  }
}

export default App;
